.button{
  cursor: pointer;
  color: var(--primary-color-inverted);
  border: none;
  padding: 10px;
  opacity: 1;
  transition: .25s linear opacity;
  &:hover{
    opacity: .75;
  }
  &--clear{
    border: none;
    background: none;
    outline: none;
  }
  &--outline{
    border: 1px solid var(--primary-color) ;
    color: var(--primary-color);
    background: none;
  }
  &--outline_red{
    border: 1px solid var(--red-dark) ;
    color: var(--red-dark);
    background: none;
  }
  &--outline_inverted{
    border: 1px solid var(--primary-color-inverted);
    color: var(--primary-color-inverted);
    background: none;
  }
  &--background{
    color: var(--primary-color);
    background-color: var(--bg-colo);
  }
  &--background_inverted{
    color: var(--primary-color-inverted);
    background-color: var(--bg-color-inverted);
  }
  &--medium{
    font: var(--font-m);
  }
  &--large{
    font: var(--font-l);
  }
  &--extra-large{
    font: var(--font-xl);
  }
  &--disabled{
    opacity: .5;
  }
}